// Настя
.section__footer {
  padding: 0;
  background: linear-gradient(to top, $title-color 70px, $white 70px, $white 100%);
  @media screen and (min-width: $tablet) {
    background: linear-gradient(to top, $title-color 70px, $white 70px, $white 100%);
  }
  @media screen and (min-width: $desktop) {
    background: linear-gradient(to top, $title-color 70px, $white 70px, $white 100%);
  }
}

.footer__container {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
  @media screen and (min-width: $tablet) {
    display: flex;
    align-items: baseline;
    padding-top: 0px;
    padding-bottom: 0px;
  }
}

.footer-list {
  display: inline-block;
  @media screen and (min-width: $tablet) {
    display: flex;
  }
  @media screen and (min-width: $desktop) {
    margin-left: 290px;
  }
}

.footer-list__item {
  display: block;
  margin-top: 31px;
  @media screen and (min-width: $tablet) {
    margin-top: 0;
  }
}

.footer-list__item--tel {
  display: block;
  margin-top: 20px;
  @media screen and (min-width: $tablet) {
    margin-top: 0;
  }
}

.footer-list__icon--tel {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  fill: $brand-color;
  @media screen and (min-width: $tablet) {
    width: 24px;
    height: 24px;
    margin-right: 15px;
  }
  @media screen and (min-width: $desktop) {
    width: 30px;
    height: 30px;
    margin-right: 15px;
  }
}

