  @media screen and (min-width: $tablet) {
    margin-top: 43px;
    margin-bottom: 42px;
    margin-left: 24px;
  }
  @media screen and (min-width: $desktop) {
    margin-top: 55px;
    margin-bottom: 55px;
  }
  &::before {
    position: absolute;
    display: block;
    content: '';
    background-color: $primary-text-color;
    width: 1px;
    height: 40px;
    left: -24px;
    display: none;
    @media (min-width: $tablet) {
      display: inline;
    }
  }
}

.footer-list__link--tel {
  display: flex;
  align-items: center;
  justify-content: center;
  color: $title-color;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.08em;
  @media screen and (min-width: $tablet) {
    margin-top: 51px;
    margin-bottom: 50px;
    font-size: 20px;
    line-height: 1.2;
    padding-right: 24px;
  }
  @media screen and (min-width: $desktop) {
    font-size: 30px;
    line-height: 1.233;
    margin-top: 56px;
    margin-bottom: 57px;
    padding-right: 16px;
  }
}

.footer-logo {
  @media screen and (min-width: $tablet) {
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
  }
  @media screen and (min-width: $desktop) {
    margin-right: 0;
  }
}

.footer-logo__link {
  display: inline-block;
  text-align: start;
  font-size: 14px;
  line-height: 1.214;
  letter-spacing: 0.08em;
  color: $title-color;
}

