.footer__text {
  text-align: center;
  padding-top: 22px;
  padding-bottom: 21px;
  font-weight: 500;
  font-size: 14px;
  line-height: 2;
  color: $primary-text-color;
  @media screen and (min-width: $tablet) {
    text-align: left;
    height: 70px;
  }
}

.footer__unicode {
  margin-right: 4px;
}
