// Алексей
.reviews {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__button {
        position: absolute;
        bottom: -62px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background-color: transparent;
        transition: 250ms $timing-fn;
        @media screen and (min-width: $tablet) {
            top: 50%;
            transform: translateY(-50%);
        }
        &--left {
            left: 0;
            @media screen and (min-width: $tablet) {
                left: -15px;
            }
            @media screen and (min-width: $desktop) {
                left: -85px;
            }
        }
        &--right {
            right: 0;
            @media screen and (min-width: $tablet) {
                right: -15px;
            }
            @media screen and (min-width: $desktop) {
                right: -85px;
            }
        }
        &:focus,
        &:hover {
            background-color: $brand-color;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
            cursor: pointer;
        }
    }
    &__button-icon {
        position: absolute;
        top: 50%;
        left: 50%;
        height: 25px;
        width: 25px;
        background-color: transparent;
        fill: $brand-color;
        transition: 250ms $timing-fn;
        transform: translate(-50%, -50%);
        .reviews__button:focus &,
        .reviews__button:hover & {
            fill: $white;
        }
    }
    &__slider {
        position: relative;
        // @media screen and (min-width: $desktop) {
        //   width: 1090px;
        // }
    }
    &__article {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding: 20px;
        padding-bottom: 30px;
        box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
        @media screen and (min-width: $tablet) {
            flex-direction: row;
            padding: 40px;
            text-align: start;
        }
        @media screen and (min-width: $desktop) {
            padding: 50px 112px 50px 82px;
        }
    }
    &__figure {
        width: 90px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        @media screen and (min-width: $tablet) {
            width: 150px;
            margin-right: 46px;
            margin-bottom: 0;
        }
        @media screen and (min-width: $desktop) {
            margin-right: 40px;
        }
    }
    &__author {
        width: 90px;
        height: 90px;
        @media screen and (min-width: $tablet) {
            width: 150px;
            height: 150px;
        }
    }
    &__icon-link {
        height: 30px;
        margin-top: 20px;
    }
    &__icon {
        width: 30px;
        height: 30px;
        fill: $vk;
    }
    &__article-text {
        @media screen and (min-width: $tablet) {
            width: 412px;
        }
        @media screen and (min-width: $desktop) {
            width: 706px;
        }
    }
    &__name {
        color: $brand-color;
        font-weight: 800;
        font-size: 14px;
        line-height: 1.25;
        letter-spacing: 0.08em;
        @media screen and (min-width: $tablet) {
            font-size: 20px;
            line-height: 1.2;
        }
        @media screen and (min-width: $desktop) {
            font-size: 30px;
            line-height: 1.25;
        }
    }
    &__description {
        margin: 10px 0;
        color: $primary-text-color;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.25;
        letter-spacing: 0.08em;
        @media screen and (min-width: $tablet) {
            margin: 20px 0;
        }
        @media screen and (min-width: $desktop) {
            margin-top: 10px;
            font-size: 14px;
            line-height: 1.2;
            width: 438px;
        }
    }
    &__text {
        color: $secondary-text-color;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.67;
        letter-spacing: 0;
        @media screen and (min-width: $desktop) {
            font-size: 18px;
            line-height: 1.56;
        }
    }
    &__hidden {
        display: none;
    }
    &__link {
        color: $brand-color;
        text-decoration: underline;
    }
    &__list {
        width: 182px;
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
        @media screen and (min-width: $tablet) {
            width: 558px;
            margin-top: 30px;
        }
        @media screen and (min-width: $desktop) {
            margin-top: 40px;
        }
    }
    &__list-item:nth-child(n + 4) {
        display: none;
        @media screen and (min-width: $tablet) {
            display: inline;
        }
    }
    &__list-thumb {
        width: 54px;
        height: 54px;
        border-radius: 50%;
        overflow: hidden;
        background-color: transparent;
        &--current {
            border: 1px solid $brand-color;
        }
    }
    &__list-photo {
        width: 54px;
        height: 54px;
    }
}