// Настя
.form-registration__section {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: $mobile;
  @media screen and (min-width: $tablet) {
    padding-bottom: 71px;
    max-width: $tablet;
  }
  @media screen and (min-width: $desktop) {
    max-width: 1680px;
    padding-top: 100px;
    padding-bottom: 62px;
  }
}

.form-registration__section {
  background-color: $title-color;
  background-repeat: no-repeat;
  background-position: 80% 50%;
  background-size: cover;
  background-image: linear-gradient(to right, rgba(17, 21, 24, 0.8), rgba(17, 21, 24, 0.8)),
    url(./../images/contacts-img/mob_1.jpg);
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: linear-gradient(to right, rgba(17, 21, 24, 0.8), rgba(17, 21, 24, 0.8)),
      url(./../images/contacts-img/mob_2@2x.jpg);
  }
  @media screen and (min-width: $tablet) {
    background-image: linear-gradient(to right, rgba(17, 21, 24, 0.8), rgba(17, 21, 24, 0.8)),
      url(./../images/contacts-img/tab_1.jpg);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: linear-gradient(to right, rgba(17, 21, 24, 0.8), rgba(17, 21, 24, 0.8)),
        url(./../images/contacts-img/tab_2@2x.jpg);
    }
    @media screen and (min-width: $desktop) {
      background-image: linear-gradient(to right, rgba(17, 21, 24, 0.8), rgba(17, 21, 24, 0.8)),
        url(./../images/contacts-img/desktop_1.jpg);
      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: linear-gradient(to right, rgba(17, 21, 24, 0.8), rgba(17, 21, 24, 0.8)),
          url(./../images/contacts-img/desktop_2@2x.jpg);
      }
    }
  }
}

.section__title--white {
  color: $white;
  font-size: 14px;
  line-height: 1.357;
  letter-spacing: 0.03em;
  margin-bottom: 30px;
  letter-spacing: 0.03em;
  margin-left: auto;
  margin-right: auto;
  width: 280px;
  @media screen and (min-width: $mobile) {
    width: 280px;
  }
  @media screen and (min-width: $tablet) {
    font-size: 26px;
    line-height: 1.307;
    width: 688px;
  }
  @media screen and (min-width: $desktop) {
    font-size: 46px;
    line-height: 1.304;
    width: 1090px;
    margin-bottom: 50px;
  }
}

.section__title--orange {
  color: $brand-color;
}

.form-registration {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.form-registration__text {
  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.08em;
  display: inline-block;
  text-align: center;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  width: 280px;
  @media screen and (min-width: $mobile) {
    width: 280px;
  }
  @media screen and (min-width: $tablet) {
    font-size: 14px;
    line-height: 1.214;
    margin-bottom: 40px;
    width: 688px;
  }
  @media screen and (min-width: $desktop) {
    font-size: 30px;
    line-height: 1.233;
    margin-bottom: 56px;
    width: 1090px;
  }
}

.form-registration__text--orange {
  color: $brand-color;
}

.form-registration__label {
  padding: 0;
  margin: 0;
}

.form-registration__input {
  background-color: transparent;
  color: $white;
  padding-top: 15px;
  padding-left: 28px;
  padding-bottom: 16px;
  width: 100%;
  letter-spacing: 0.08em;
  border: 1px solid $white;
  margin-left: auto;
  margin-right: auto;
  // @media screen and (min-width: $mobile) {
  //     width: 280px;
  // }
  @media screen and (min-width: $tablet) {
    width: 398px;
    height: 48px;
  }
  @media screen and (min-width: $desktop) {
    width: 478px;
    height: 70px;
    padding-top: 23px;
    padding-left: 40px;
    padding-bottom: 23px;
  }
  &::placeholder {
    font-size: 14px;
    line-height: 1.214;
    color: inherit;
    border: 1px $white;
    opacity: 1;
    @media screen and (min-width: $desktop) {
      font-size: 20px;
      line-height: 1.2;
      letter-spacing: 0.08em;
    }
  }
  &:valid {
    border-color: inherit;
  }
  &:invalid {
    border-color: rgb(255, 0, 0);
  }
}

.form-registration__input--name {
  margin-bottom: 20px;
  @media screen and (min-width: $desktop) {
    margin-bottom: 30px;
  }
}

.form-registration__input--tel {
  margin-bottom: 50px;
  @media screen and (min-width: $tablet) {
    margin-bottom: 31px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 80px;
  }
}

.service-button__form {
  display: block;
  margin-left: auto;
  margin-right: auto;

  &:hover,
  &:focus {
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);;
  }

  @media screen and (min-width: $desktop) {
    width: 354px;
    height: 70px;
    font-size: 14px;
    line-height: 1.214;
    letter-spacing: 0.14em;
  }
}

.div {
  outline: solid rebeccapurple;
}

.div:first-child {
  display: block;
  border-left: 3px solid red;
}
