.menu__button {
  width: 260px;
  display: flex;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
  margin-top: 50px;
  padding: 14px 0;
  background-color: transparent;
  border: 2px solid $brand-color;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: $brand-color;
  transition: background-color 250ms $timing-fn, color 250ms $timing-fn,
    border-color 250ms $timing-fn;

  &:hover,
  &:focus {
    background-color: $brand-color;
    color: $white;
    border-color: $white;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
    cursor: pointer;

    // Второй варинат закраски кнопки
    // &:hover,
    // &:focus {
    // // background-color: $brand-color;
    // color: $white;

    // transition: background-color 250ms, color 250ms $timing-fn;
    // }
  }
}

@media (min-width: $tablet) {
  .menu__button {
    display: none;
  }
}

@media screen and (min-width: $tablet) {
  .content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
