// Андрей
html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.guarantee {
    background-color: $white;
}

.guarantee__container {
    width: 100%;
    margin: 0 auto;
    @media screen and (min-width: $mobile) {
        width: $mobile;
    }
    @media screen and (min-width: $tablet) {
        width: $tablet;
        padding-left: 40px;
        padding-right: 40px;
    }
    @media screen and (min-width: $desktop) {
        width: $desktop;
        padding: 0 15px;
    }
}

.guarantee__title {
    margin: 0 0 30px 0;
    font-size: 26px;
    font-weight: 800;
    line-height: 1.3;
    letter-spacing: 0.03em;
    color: $title-color;
    text-align: center;
    @media screen and (min-width: $desktop) {
        margin-bottom: 50px;
        font-size: 46px;
    }
}

.guarantee__list {
    padding: 30px 20px 31px 20px;
    background-color: $guarantee-section-background-color;
    @media screen and (min-width: $tablet) {
        padding: 30px 50px 30px 79px;
    }
    @media screen and (min-width: $desktop) {
        padding: 50px 112px 45px 112px;
    }
}

.guarantee__item {
    position: relative;
    &:not(:last-child) {
        margin-bottom: 23px;
    }
    @media screen and (min-width: $tablet) {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    &:before {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        content: '';
        width: 15px;
        height: 20px;
        background-image: url('../images/guarantee/fire.svg');
        background-repeat: no-repeat;
        background-size: contain;
        @media screen and (min-width: $desktop) {
            width: 22px;
            height: 30px;
        }
    }
    &:not(:last-child)::after {
        position: absolute;
        display: block;
        content: '';
        width: 1px;
        height: 68px;
        background-image: url('../images/guarantee/line.svg');
        background-repeat: no-repeat;
        top: 25px;
        left: 7px;
        @media screen and (min-width: $desktop) {
            top: 34px;
            left: 11px;
            height: 40px;
        }
    }
    // Костыль - решение проблемы с высотой пунктирной линии от огня к огню
    &:nth-child(1)::after {
        @media screen and (min-width: 340px) {
            height: 51px;
        }
        @media screen and (min-width: 453px) {
            height: 30px;
        }
        @media screen and (min-width: $desktop) {
            top: 34px;
            left: 11px;
            height: 40px;
        }
    }
    &:nth-child(2)::after {
        @media screen and (min-width: 348px) {
            height: 51px;
        }
        @media screen and (min-width: 449px) {
            height: 30px;
        }
        @media screen and (min-width: $desktop) {
            top: 34px;
            left: 11px;
            height: 40px;
        }
    }
    &:nth-child(3)::after {
        @media screen and (min-width: 325px) {
            height: 51px;
        }
        @media screen and (min-width: 444px) {
            height: 30px;
        }
        @media screen and (min-width: $desktop) {
            top: 34px;
            left: 11px;
            height: 40px;
        }
    }
}

.guarantee__info {
    margin: 0;
    padding-left: 35px;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.67;
    letter-spacing: 0;
    color: $secondary-text-color;
    @media screen and (min-width: $tablet) {
        padding-left: 39px;
    }
    @media screen and (min-width: $desktop) {
        padding-left: 52px;
        font-size: 20px;
        line-height: 1.4;
    }
}

.guarantee__remark {
    color: $brand-color;
}