// Юля
.dodo {
    width: 100%;
    padding-bottom: 50px;

    @media screen and (min-width: $desktop) {
        padding-bottom: 130px;
    }
}

.dodo__container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
    @media screen and (min-width: 480px) {
        width: 480px;
    }
    @media screen and (min-width: $tablet) {
        width: $tablet;
        padding-left: 40px;
        padding-right: 40px;
    }
    @media screen and (min-width: $desktop) {
        width: $desktop;
        padding-left: 15px;
        padding-right: 15px;
        padding-bottom: 0;
    }
}

.thumb__dodo {
    height: 48px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-bottom: 20px;
    color: $brand-color;
    .dodo__item:hover~& {
        color: $white;
    }
}

@media screen and (min-width: $tablet) {
    .thumb__dodo {
        margin-bottom: 30px;
    }
}

.dodo__icon {
    fill: currentColor;
    height: 48px;
}

.dodo__title {
    font-size: 20px;
    font-weight: 800;
    line-height: 1.3;
    letter-spacing: 0.03em;
    color: #111518;
    margin-top: 0;
    margin-bottom: 30px;
    text-align: center;
    @media screen and (min-width: $tablet) {
        font-size: 26px;
    }
    @media screen and (min-width: $desktop) {
        font-size: 46px;
        text-align: start;
        margin-bottom: 50px;
    }
}

.dodo__list {
    margin: 0;
    padding: 0;
    list-style: none;
    @media screen and (min-width: $tablet) {
        display: flex;
        flex-wrap: wrap;
        margin-left: -20px;
        margin-top: -27px;
    }
    @media screen and (min-width: $desktop) {
        margin-top: -20px;
    }
}

.dodo__item {
    position: relative;
    padding: 30px 6px 22px 6px;
    max-width: 100%;
    height: 280px;
    background-color: #ffffff;
    box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
    transition: background-color 250ms;
    &:not(:last-child) {
        margin-bottom: 15px;
    }
    &:hover,
    &:focus {
        background-color: #ff6b09;
        color: $white;
        box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.2);
        .dodo__item-header {
            color: #ffffff;
        }
        .dodo__dash {
            color: #ffffff;
        }
        .dodo__info {
            color: #ffffff;
        }
    }
    &:hover .thumb__dodo,
    &:focus .thumb__dodo {
        color: $white;
        transition: color 250ms;
    }
    @media screen and (min-width: $tablet) {
        flex-basis: calc(100% / 2 - 20px);
        height: 334px;
        width: 280px;
        margin-left: 20px;
        margin-top: 27px;
        padding: 40px 20px 20px 20px;
        &:not(:last-child) {
            margin-bottom: unset;
        }
    }
    @media screen and (min-width: $desktop) {
        flex-basis: calc(100% / 3 - 20px);
        height: 396px;
        margin-top: 20px;
        padding: 40px 30px 55px 30px;
    }
}

.dodo__img {
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    &:nth-child(3) {
        color: #ff6b09;
    }
}

.dodo__item-header {
    margin: 0;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.43;
    letter-spacing: 0.08em;
    color: #111518;
    text-align: center;
    transition: color 250ms;
    @media screen and (min-width: $tablet) {
        margin-bottom: 10px;
    }
    @media screen and (min-width: $desktop) {
        font-size: 20px;
        line-height: 1.4;
        margin-bottom: unset;
    }
}

.dodo__item-header--span {
    display: block;
}

.dodo__dash {
    display: block;
    font-size: 20px;
    font-weight: 700;
    line-height: 1.65;
    letter-spacing: 0.08em;
    color: #ff6b09;
    text-align: center;
    transition: color 250ms;
    @media screen and (min-width: $tablet) {
        margin-bottom: 10px;
    }
    @media screen and (min-width: $desktop) {
        margin-bottom: unset;
    }
}

.dodo__info {
    margin: 0;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.67;
    letter-spacing: 0.08em;
    color: #b9b9b9;
    text-align: center;
    transition: color 250ms;
    @media screen and (min-width: $desktop) {
        font-size: 18px;
    }
}